/**
 * White theme for reveal.js. This is the opposite of the 'black' theme.
 *
 * By Hakim El Hattab, http://hakim.se
 */


// Default mixins and settings -----------------
@import "reveal.js/css/theme/template/mixins";
@import "reveal.js/css/theme/template/settings";
// ---------------------------------------------


// Include theme-specific fonts
@import url('https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


// Override theme settings (see ../template/settings.scss)
$backgroundColor: #fff;

$mainColor: #222;
$headingColor: #222;

$mainFontSize: 38px;
$mainFont: 'Bitter', serif;
$headingFont: 'Bitter', serif;
$headingTextShadow: none;
$headingLetterSpacing: normal;
$headingTextTransform: uppercase;
$headingFontWeight: 600;
$linkColor: #007e00;
$linkColorHover: lighten( $linkColor, 15% );
$selectionBackgroundColor: lighten( $linkColor, 25% );

$heading1Size: 2em;
$heading2Size: 1.6em;
$heading3Size: 1.3em;
$heading4Size: 1.0em;

// Change text colors against dark slide backgrounds
@include dark-bg-text-color(#fff);


// Theme template ------------------------------
@import "reveal.js/css/theme/template/theme";
// ---------------------------------------------


.reveal {
	ul {
		list-style-type: none;
		padding: 0;
		margin: 0;
	}

	li {
		/*padding-left: 2em;*/
	}

	ul li::before {
		content: "â€¢";
		color: $linkColor;
		padding-right: 0.5em;
	}
	.top {
		top: 20px;
		position: fixed;
		font-size: 0.42em;
		color: #747474;
		z-index: 10;
	}

	.left {
		left: 20px;
		text-align: left;
	}

	.right {
		right: 20px;
		text-align: right;
	}

	.footer-left {
		color: #111;
		/*     font-family    : "League Gothic", Impact, sans-serif;*/
		letter-spacing: 0.02em;
		line-height: 0.9em;
		margin: 0 0 20px;
		text-shadow: 0 0 6px rgba(255, 255, 255, 0.2);
		bottom: 0px;
		text-align: left;
		padding-left: 5px;
		position: absolute;
		left: 0; }

	.footer-right {
		color: #111;
		/*     font-family    : "League Gothic", Impact, sans-serif;*/
		letter-spacing: 0.02em;
		line-height: 0.9em;
		margin: 0 0 20px;
		text-shadow: 0 0 6px rgba(255, 255, 255, 0.2);
		bottom: 0px;
		text-align: right;
		padding-right: 5px;
		position: absolute;
		right: 0; }

	dl.frontmatter {
		margin-left: 150px;
		font-size: 1.5rem; }

	.frontmatter dt {
		font-style: normal;
		font-weight: 300;
		font-size: 0.8em;
		width: 150px;
		float: left;
		text-align: right; }

	.frontmatter dt:after {
		content: ":"; }

	.frontmatter dd {
		margin-left: 160px; }

	.frontmatter dd:first-of-type {
		font-size: 1.5em; }

	img {
		max-height: 600px; }

	img[src^="../resources/timezones201"] {
		max-height: 250px; }

	img[src^="resources/timezones201"] {
		max-height: 250px; }

	pre {
		width: 100%;
	}
}

div.multicolumn {
	-moz-column-count: 2;
	-moz-column-gap: 50%;
	-webkit-column-count: 2;
	-webkit-column-gap: 50%;
	column-count: 3;
	column-gap: 50%; }

span.credit {
	font-size: 12px;
	line-height: 1em;
	margin-top: -20px;
	position: relative;
	display: block;
	text-align: right;
	color: gray; }

span.credit a {
	color: gray; }

div.multicolumn {
	-moz-column-count: 2;
	-moz-column-gap: 1%;
	-webkit-column-count: 2;
	-webkit-column-gap: 1%;
	column-count: 2;
	column-gap: 1%;
}

.corner-logo {
	position: fixed;
	bottom: 30px;
	left: 30px;
	width: 200px;
}
